@keyframes FullColorRotate {
  0% {
    color: #ff0000;
    stroke: #ff0000;
    fill: #ff0000;
    /*background-color: #00ffff;*/
    text-shadow: 3px 3px 0 #00ffff;
    box-shadow: 3px 3px 0 #00ffff;
  }

  17% {
    color: #ffff00;
    stroke: #ffff00;
    fill: #ffff00;
    /*background-color: #0000ff;*/
    text-shadow: 3px 3px 0 #0000ff;
