  left: 0;
  z-index: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: 300ms;
  pointer-events: none;
}

.modal.displayModal {
  opacity: 1;
  pointer-events: auto;
}
.modalBackdrop {
  position: absolute;
  height: 100vh;
  width: 100vw;
  background-color: #000000;
  opacity: 0.8;
  cursor: pointer;
}


.modalContent {
  min-width: 300px;
  max-width: 1000px;
  background-color: #fff;
  z-index: 501;
  border: 2px solid;
  padding: 20px;
  padding-top: 0;
}

