  padding: 0.3em;
  font-size: 25px;
  border: 2px solid;
  border-radius: 2px;
  display: block;
  margin: auto;
  width: 280px;
  transition: 300ms;
}

.signupButton {
  cursor: pointer;
  transition: 200ms;
  font-size: 16px;
  padding: 0.4em;
  border: 2px solid;

}
.signupButton:hover {
  background-color: #000;
  border: 2px solid #000;
  color: #fff;
}

.modalInput:focus, .modalInput:active {
  background-color: #000;
  border-color: #000;
  color: #fff;
}

.modalTitle {
